import Login from './views/Login.vue'
import Register from './views/Register.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Index from './views/user/Index.vue'
import Main from './views/user/Main.vue'
import Detail from './views/user/Detail.vue'
import word from './views/user/word.vue'
import allword from './views/user/allword.vue'
import wordGroup from './views/user/wordGroup.vue'
import mywordGroup from './views/user/myWordGroup.vue'
import download from './views/user/download.vue'
import test from './views/user/test.vue'
import oldwordtest from './views/user/oldwordtest.vue'
import List from './views/user/List.vue'
import CLUB from './views/nav1/CLUB.vue'
import Active from './views/nav1/Active.vue'
import user from './views/nav1/user.vue'
import Apply from './views/nav2/Page5.vue'


let routes = [
    {
        path: '/index',
        component: Index,
        leaf: true,//
        iconCls:'el-icon-house',
        name:'',
        children: [
            { path: '/main', component: Main, name: '' },
        ],
        isTrue:true,
        hidden:true
    },
    {
        path: '/index',
        component: Index,
        leaf: true,//
        iconCls:'el-icon-s-custom',
        name:'',
        children: [
            { path: '/list', component: List, name: '' },
        ],
        isTrue:true,
        hidden:true
    },
    {
        path: '/index',
        component: Index,
        leaf: false,//
        iconCls:'el-icon-s-custom',
        name:'',
        children: [
            { path: '/detail', component: Detail, name: '' },
        ],
        isTrue:false,
        hidden:true
    },
    // {
    //     path: '/index',
    //     component: Index,
    //     leaf: false,//
    //     iconCls:'el-icon-s-custom',
    //     name:'',
    //     children: [
    //         { path: '/userInfo', component: userInfo, name: '' },
    //     ],
    //     isTrue:false,
    //     hidden:true
    // },
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/register',
        component: Register,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            // { path: '/main', component: Main, name: '' },
            // { path: '/table', component: Table, name: 'Table' },
            // { path: '/club', component: CLUB, name: '' },
            { path: '/word', component: word, name: '' },
            { path: '/allword', component: allword, name: '' },
            { path: '/wordGroup', component: wordGroup, name: '' },
            { path: '/myWordGroup', component: mywordGroup, name: '' },
            { path: '/test', component: test, name: '' },
            { path: '/review', component: oldwordtest, name: '' },
            { path: '/download', component: download, name: '' },
            
            
            // { path: '/user', component: user, name: '' },
        ]
    },
    // {
    //     path: '/',
    //     component: Home,
    //     name: '',
    //     iconCls: 'fa fa-id-card-o',
    //     children: [
    //         { path: '/active', component: Active, name: '' },            
    //         { path: '/apply', component: Apply, name: '' }
    //     ]
    // },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/login' }
    },
   
];

export default routes;


